import moment from 'moment'

export default class Tick {
  constructor() {
    this.ticts = {}
  }

  createTick(id, dealLine) {
    const diff = moment(dealLine).diff(moment())
    const _ticts=this.ticts[id] = {
      dealLine: dealLine,
      id: id,
      diff: diff,
      interval: setInterval(() => {
        let t  = _ticts.diff / 1000  // js默认时间戳为毫秒,需要转化成秒
        let d = Math.floor(t / (24 * 3600))
        let h = Math.floor((t - 24 * 3600 * d) / 3600)
        let m = Math.floor((t - 24 * 3600 * d - h * 3600) / 60)
        let s = Math.floor((t - 24 * 3600 * d - h * 3600 - m * 60))

        // 这里可以做一个格式化的处理
        document.getElementById(id).innerHTML = d + '天' + h + '小时' + m + '分钟' + s + '秒'
        _ticts.diff -= 1000
        if (_ticts.diff < 0)
          this.deleteTick(id)  // 判断是否到期,到期后自动删除定时器
      }, 1000)
    }
  }

  deleteTick(id) {
    clearInterval(this.ticts[id].interval) // 清楚定时器的方法,需要定时器的指针作为参数传入clearInterval
    delete this.ticts[id]  // 通过delete的方法删除对象中的属性
  }
}





